.btn {
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
    cursor: pointer;
    width: 34px;
    height: 34px;
    left: 50px;
    border-radius: 50%;
    border: none;

    background: #477577;
}

.btn:active {
    background: #edf5f5;
    color: darkgrey;
}

.swiper_nav_btns {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 16px;
}

@media screen and (min-width: 1440px) {
    .swiper_nav_btns {
        flex-direction: row;
        justify-content: space-between;
        position: absolute;
        z-index: 1000;
        top: 50%;
        min-width: 1200px;
        bottom: 50%;
    }
}
